.customize-control-arrowpress-dimension{
    .arrowpress-control-form{
        .arrowpress-input-control{
            +  .arrowpress-control-label{
                font-size: 10px;
                font-weight: 500;
                line-height: normal;
                text-align: center;
                text-transform: uppercase;
                color: #777;
                overflow-wrap: anywhere;
            }
        }
    }

	.arrowpress-control-input{
		padding: 0 8px;
		line-height: 2;
		min-height: 30px;
		font-size: 14px;
	}
}
